.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.box{
  width:100%;
  
  min-height:30vh;
  display: flex;
  border-radius: 10px;
  
  align-items: center;
  flex-direction: row;
  margin-bottom: 0vh;
  justify-content: space-evenly;
}

#heropic{
  width: 100vw;
  height: 70vh;
  background-color: grey;
  background-image: url("./images/code.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-evenly;


}
#welcometext{
  
  font-size: 150%;
  font-family: Impact, sans-serif;
 
  font-weight: 900;
  color: grey;

}

.divider{
  min-width: 0.5vh;
  background-color: #e0e0eb;
  min-height: 20vh;
  border-radius: 10px;

}

#content{

  display: flex;
  flex-direction: column;
  padding: 5vh;
  align-items: stretch;
  justify-content: space-between;
  

}

.text{
  width:100%;
  height:20vh;
  background-color: white;
  border-radius: 10px;

}

.picture{
  width:100%;
  height:20vh;
  background-color: white;
  border-radius: 10px;
  margin-left: 10vh;
  margin-right: 30vh;

}

