.navbar{
  width: 100%;
  max-width: 100%;
  min-height: 10vh;
  box-sizing: border-box;
  display: flex;
  z-index: 100;
  color: black;



  background-color: #f0f0f5;
  transition: all .2s ease;
}

.scrolled{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #e0e0eb;
}



    



.navigation{
    width: 100%;


  ul{
    box-sizing: border-box;
    list-style-type: none;
    display: flex;
    min-height: 3vh;
    width: 100%;
    justify-content: space-between;


    li{
      text-align: center;
      padding: 0em;

 
/*       cursor: pointer;
      &:hover{
        background-color: #3d3d5c;
        color: white;
        position: relative;
        top: 3vh;
      } */
    }

    li a{
      text-decoration: none;
      color: inherit;
    }

  }
}

#logo{
  background-image: url("./images/BCALogoMk4.svg");
  height: 10vh;

  width:15vw;
 



  background-size: contain;
  background-repeat: no-repeat;

  position: relative;

}

#contacts{
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: #33334d;
  font-size: large;
  font-weight: 300;
  margin-right: 5vh;

}